<script type="text/javascript">
const acg_colos = [
  "#e62d73",
  "#0089ff",
  // "#b3a700",
  "#fff233",
  "#19ffa8",
  "#8600cc",
  "#00024d",
  "#fbd7be",
];
const acg_colos_info = [
