<template>
  <el-row>
    <el-col :span="8">
      <el-card style="border: none" :body-style="{ padding: '0px' }">
        <div style="position: relative">
          <img
            draggable="false"
            v-bind:src="getimg(this.acgdata.AID)"
            :onerror="defalutLogoUrl"
            class="image"
            width="100%"
            style="border-radius: 3px 3px 0px 0px"
          />
        </div>
        <div id="Title">
          <span>{{ this.acgdata.Title }}</span>
        </div>
      </el-card>
    </el-col>
    <el-col :span="16">
      <el-row>
        <el-col :xs="24" :sm="12" :md="8">
          <a
            :href="this.acg_web_url.acgfun + '/detail/' + this.acgdata.AID"
            target="_blank"
          >
            <el-button type="primary" plain>AcgFun </el-button>
          </a>
        </el-col>
        <el-col :xs="24" :sm="12" :md="8">
          <a
            :href="this.acg_web_url.omofun + this.acgdata.Title"
            target="_blank"
          >
            <el-button type="primary" plain>OmoFun</el-button></a
          >
        </el-col>
        <el-col :xs="24" :sm="12" :md="8">
          <a
            :href="this.acg_web_url.yinghuacd + this.acgdata.Title"
            target="_blank"
          >
            <el-button type="primary" plain>樱花动漫</el-button></a
          >
        </el-col>
        <el-col :xs="24" :sm="12" :md="8">
          <a
            :href="this.acg_web_url.bimiacg + this.acgdata.Title"
            target="_blank"
          >
            <el-button type="primary" plain>Bimiacg</el-button></a
          >
        </el-col>
        <el-col :xs="24" :sm="12" :md="8">
          <a
            :href="this.acg_web_url.dilidili + this.acgdata.Title"
            target="_blank"
          >
            <el-button type="primary" plain>Dilidili</el-button></a
          >
        </el-col>
        <el-col :xs="24" :sm="12" :md="8">
          <a
            :href="this.acg_web_url.bilibili + this.acgdata.Title"
            target="_blank"
          >
            <el-button type="primary" plain>Bilibili</el-button></a
          >
        </el-col>
      </el-row>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DefaultData from "./DefaultData.vue";

export default {
  name: "AltConnect",

  props: {
    acgdata: [],
  },
  data() {
    return {
      acg_web_url: DefaultData.acg_web_url,
      imgURL: "",
      mian_url: DefaultData.Main_url,
      defalutLogoUrl: DefaultData.errorimg,
    };
  },

  mounted() {},

  methods: {
    getimg(index) {
      var _this = this;

      if (this.acgdata.Title == undefined) {
        // eslint-disable-next-line vue/no-mutating-props
        this.acgdata.Title = this.acgdata.R动画名称;
      }

      axios
        .get("https://api.agefans.app/v2/detail/" + index)
        .then((response) => {
          _this.imgURL = response.data.AniInfo.R封面图;
        });
      return _this.imgURL;
    },
  },
};
</script>

<style lang="scss" scoped>
a {
  color: #4297e0;
  text-decoration: none;
}
.el-button {
  width: 100px;
  margin: 5px;
}
</style>